html {
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

  /* For some Androids */
  -webkit-tap-highlight-color: transparent;

  /* font-size: 1rem; */
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
body {
  width: 100%;
  height: 100%;
  -webkit-touch-callout: none;

  /* prevent callout to copy image, etc when tap to hold */
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;

  /* prevent webkit from resizing text to fit */
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

  /* prevent copy paste, to allow, change 'none' to 'text' */
  background-color: transparent;
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  overflow: hidden;

  /* font-family: Arial; */
}
input,
textarea,
select {
  font-family: inherit;
}
.pageBody {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}
.cubesketcherContainer {
  display: block;
}
.cubesketcherCubeContainer {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.cubesketcherSketchContainer {
  height: calc(100% - 48px);
}
.cubesketcherSearchContainer {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
}
.cubesketcherSearchTitle {
  height: 170px;
  width: 100%;
}
.header {
  position: relative;
  top: 0;
  height: 48px;
  width: 100%;
  background-color: #9ab6ca;
}
.backButton {
  background: url(../img/back_icon-96x96.png);
  position: absolute;
  left: 0;
  bottom: 0;
  width: 48px;
  height: 48px;
  background-size: 48px;
  cursor: pointer;
}
.title {
  position: absolute;
  color: #ffffff;
  font-weight: bold;
  left: 48px;
  right: 48px;
  text-overflow: ellipsis;
  height: 100%;
  text-align: center;
  line-height: 48px;
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
}
.drawings {
  padding: 8px;
  padding-left: calc(50% - 153px);
}
.drawing {
  position: relative;
  height: 100px;
  width: 100px;
  border: 1px solid #000000;
}
.cubesketcherSearchResult {
  height: calc(100% - 200px);
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}
